import { mapState } from 'vuex';
export default {
  data() {
    return {
    };
  },
  computed: {
    ...mapState({
      count:state => state.shop.count,
      goods:state => state.shop.goodsDeatils,
    })
  },
  methods:{
    handleClick(){
         this.$emit('upup');
    },
    addCartClick(data){
      this.$store.commit("addShopCart", data);
      this.$store.commit("addCartClick");
    }
  }
};



